.skills__section {
  min-height: 100%;
  display: grid;
  grid-template-columns: 2fr 1fr;
  background: linear-gradient(180deg, rgb(39, 39, 39) 96%, #250705 100%);
  //display: flex;
  justify-items: center;
  align-items: center;
  .zurks_container {
    width: 100%;
    height: 85%;
    margin: auto;
    position: absolute;
    z-index: -1;
    > img {
      position: relative;
    }
  }
  .sills_leftSide {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr 4fr 1fr;
    width: 95%;
    height: 75vh;
    background: #3e3d3df2;
    margin: 0 auto;
    border-radius: 10px;
    .LibraryH1 {
      grid-area: 1 / 1 / 2 / 6;
      align-items: center;
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      font-size: calc(var(--index) * 1.5);
    }
    .coming_soon {
      display: flex;
      grid-area: 3/3/4/4;
      position: relative;
      right: calc(var(--index) * 1.5);
      width: 100%;
      font-size: calc(var(--index) * 0.3);
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;
      > h2 {
        opacity: 50%;
      }

    }
    .skills_library {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      grid-area: 2/1/3/4;
      justify-items: center;
      align-items: center;
      .skills_project {
        display: flex;
        position: relative;
        margin: 0 auto;
        justify-content: center;
        min-width: calc(var(--index) * 11);
        height: calc(var(--index) * 6);
        align-items: center;
        font-size: calc(var(--index) * 0.9);
        transition: all 200ms ease;
        img {
          width: 100%;
          height: 100%;
          z-index: 7;
          border: #9d4008 solid 1px;
          filter: opacity(0.5) grayscale(0.8);
          border-radius: 5px;
          transition: all 200ms ease;
        }
        h1 {
          z-index: 10;
          position: absolute;
          color: var(--text);
          font-weight: 300;
          font-family: 'Oxanium', cursive;
          text-shadow: 0 0 15px #471F13;
          filter: opacity(1) grayscale(0);
          transition: all 200ms ease;
        }
        &:hover {
          transform: scale3d(1.2,1.2,1.2);
          img {
            filter: opacity(1) grayscale(0);
          }
          h1 {
            filter: opacity(0);
            pointer-events: none;
          }
          cursor: pointer;
        }
      }
    }
    img {
      position: absolute;
    }
  }
  .section_right {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 5fr;
    max-width: 100%;
    height: 75vh;
    margin: 0 1.5vw;
    background: rgba(62, 61, 61, 0.9490196078);
    border-radius: 10px;
    .skill_box {
      display: flex;
      align-items: center;
      justify-items: center;
      flex-direction: column;
      padding: 0 2vw;
    }
    .SkillsH1 {
      display: flex;
      align-items: center;
      justify-items: center;
      margin: 0 auto;
      font-size: calc(var(--index) * 1.5);
    }
    .skill_container {
      display: flex;
      justify-items: center;
      align-items: flex-start;
      flex-direction: column;
      height: 100%;
      justify-content: center;
    }
    .skill {
      display: inline-block;
      position: relative;
      width: calc(var(--index) * 18);
      height: calc(var(--index) * 1);
      margin: calc(var(--index) * 0.9) auto;
      background-size: contain;
      z-index: 12;
      border-radius: 10px;
      .skill_progress {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        border-radius: 5px;
        width: 100%;
        //background:#ff681a ;
        border: 1px solid var(--background);
        z-index: 1;
        .skill_value {
          position: absolute;
          font-family: 'Oxanium', cursive;
          font-size: calc(var(--index) * 0.5);
          transition: opacity 0.1s linear;
          text-align: center;
          opacity: 0;
        }
        .skill_progress_boxes {
          display: flex;
          justify-content: flex-start;
          margin: 0 auto;
          align-items: center;
          gap: 0 4px;
          width: 100%;
          height: 100%;

          div {
            border: 1px solid #959490;
            border-radius: 5px;
            height: calc(var(--index) * 1);
            background-color: #363633;
            box-shadow: inset 0px 0px 25px 3px rgb(0 0 0 / 50%);
            width: calc(var(--index) * 5);
          }
          .filled {
            background-color: #ff5900;
            box-shadow: inset 0px 0px 15px 1px rgb(0 0 0 / 50%);
          }
        }
      }
      &:hover {
        .skill_value {
          opacity: 1;
        }
      }
    }
    .skill_name {
      display: flex;
      align-items: center;
      font-size: calc(var(--index) * .9);
      &:before {
        content: "";
        width: calc(var(--index) * .9);
        height: calc(var(--index) * .9);
        display: inline-block;
        left: 0;
        position: relative;
        background: url("../../../assets/png/icons/pin.svg") 50% 50% no-repeat;
        background-size: cover;
        margin-right: calc(var(--index)/10);
      }
    }
  }
}
.gradient_fog_top {
  width: 100%;
  height: calc(var(--index)*10);
  transform: translateY(calc(var(--index)* -0.1));
  background: linear-gradient(180deg, rgb(39, 39, 39) 0%, rgba(255, 255, 255, 0) 45%);
}
.gradient_fog_bottom {
  width: 100%;
  height: calc(var(--index) * 10);
  transform: translateY(calc(var(--index) * 24));
  background: linear-gradient(0deg, rgb(39, 39, 39) 0%, rgba(255, 255, 255, 0) 45%);
}

