.contacts__section {
  display: block;
  background: linear-gradient(0deg, rgb(39, 39, 39) 80%, #250705 100%);
  justify-content: center;
  align-items: center;
  width: 100%;
  font-family: 'Orbitron', sans-serif;
  font-weight: 400;
  .clipBoard {
    display:flex;
    opacity: 0;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    height: var(--index);
    width: calc(var(--index) * 3.4);
    position: absolute;
    background: rgba(16, 10, 10, 0.45);
    transition: all 200ms ease-in-out;
    color: #9d4008;
  }
  textarea::-webkit-scrollbar {
    width: 0px;
  }
  input:-webkit-autofill,
  input:-webkit-autofill:focus {
    transition: background-color 600000s 0s, color 600000s 0s;
  }
  .layers {
    height: 100vh;
    width: 100%;
    text-align: center;
    overflow: hidden;
    position: absolute;
    .layer {
      width: 100%;
      height: 100%;
      background-size: cover;
      position: absolute;
      background-repeat: no-repeat;
    }
    .contacts__layer__base {
      background: linear-gradient(0deg,  rgba(255, 255, 255, 0) 80%, #250705 100%), url("../../../assets/png/contactsBackground_0.jpg") 50% 50%;
      background-size: cover;
    }

    .contacts__layer__middle {
      background: url("../../../assets/png/contactsLayer_2.png") 50% 50%;
      background-size: cover;
      z-index: 2;
    }

    .contacts__layer__eye {
      background: url("../../../assets/png/contactsLayer_1.png") 50% 50%;
      background-size: cover;
      z-index: 1;
    }
  }
  .contacts_form {
    display: flex;
    width: 100%;
    height: 100%;
    z-index: 2;
    .linear_background {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: calc(var(--index) * 2.2);
      height: 100%;
      left: 0;
      bottom: 0;
      background: url("../../../assets/png/contactsLinear.png") no-repeat;
      background-size: cover;
      justify-content: space-between;
      z-index: 2;
      .contacts_number_email {
        display: flex;
        flex-direction: column;
        width: calc(var(--index) * 10);
        font-size: calc(var(--index) * 0.5);
        color: rgba(255, 255, 255, 0.7);
        font-weight: 400;

        .con_top {
          display: flex;
          gap: var(--index);
          justify-content: space-between;
          margin-bottom: var(--index);
          ::-moz-selection { /* Code for Firefox */
            color: rgba(255, 255, 255, 1);
            background: #75322e;
          }
          ::selection {
            color: rgba(255, 255, 255, 1);
            background: #75322e;
          }
          p {
            pointer-events: all;
            user-select: text;
            &:hover {
              color: rgba(255, 255, 255, 1);
            }
          }
          .contacts_h3 {
            font-size: calc(var(--index) * 0.5);
            color: rgba(255, 255, 255, 0.5);
            font-weight: 400;
          }
        }
        .contacts__number {
          display: inline-flex;
          justify-content: flex-start;
          gap: calc(var(--index)/1.4);
        }

      }
      .contacts_sendMessage {
        .send_form {
          display: flex;
          flex-direction: column;
          gap: calc(var(--index) * 1.2);
          h2 {
            font-size: calc(var(--index) * .9);
            font-weight: 400;
          }
          div {
            input {
              background: none;
              border: none;
              font-family: 'Oxanium', sans-serif;
              font-weight: 400;
              border-bottom: #959490 1px solid;
              color: rgba(255, 255, 255, 0.7);
              font-size: calc(var(--index) * 0.7);
              width: calc(var(--index) * 11);
              margin-top: calc(var(--index) * 0.3);
              min-height: calc(var(--index));
              &:focus-visible {
                outline: none;
              }
            }
          }

          #send_message {
            max-height: calc(var(--index) * 15);
            font-family: 'Oxanium', sans-serif;
            font-weight: 400;
            background: none;
            border: none;
            border-bottom: #959490 1px solid;
            color: rgba(255, 255, 255, 0.7);
            width: calc(var(--index) * 11);
            font-size: calc(var(--index) * 0.7);
            outline: none;
            margin-top: calc(var(--index) * 0.3);
            resize: none;
            &:focus-visible {
              outline: none;
            }
          }
          .wrong {
            margin: calc(var(--index)/ 4) 0;
            font-size: calc(var(--index) * 0.5);
            color: #ee050d;
          }


          .send_button {
            display: flex;
            justify-content: center;
            align-items: center;
            height: calc(var(--index) * 1.8);
            width: calc(var(--index) * 5.4);
            margin-top: calc(var(--index) * 0.3);
            background: rgba(83, 10, 10, 0.21);
            border: 3px solid #C58610;
            color: rgba(255, 255, 255, 0.7);
            transition: all 150ms ease;
            &:disabled {
              background: rgb(70 70 70 / 21%);
              border: 3px solid #959595;
              &:hover {
                background: rgb(70 70 70 / 21%);
              }
            }
            > h2 {
              font-size: calc(var(--index) * .7);
              font-weight: 400;
            }
            &:hover {
              background: rgba(218, 40, 40, 0.21);
            }
          }
          .send_button.success {
              background: rgba(142, 255, 114, 0.2);
              border: 3px solid rgba(28, 83, 10, 0.75);
          }
        }
      }
    }

  }
}
