//Tablet



@media screen and (max-width: 1024px) {

  .skills_library {

  }
}

@media screen and (max-width: 768px) {
  .main-header {
    .layers {
      .layer__base {
        background-position: 50% 100%;
      }
      .layer__middle {
        background-position: 50% 100%;
      }
      .layer__header {
        padding-bottom: calc(var(--index) * 20);

      }
    }
  }
  #CanvasSection {
    flex-direction: column;
    padding: 10vh 0;
    .dialog__container {
      width: 80%;
    }
  }
  .skills__section {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
    height: 100%;
    .sills_leftSide {
      grid-area: 1 / 1 / 2 / 2;
      height: calc(var(--index) * 27);
      .LibraryH1 {
        font-size: var(--index);
      }
      .skills_library {
        grid-row-gap: var(--index);
        .skills_project {
          min-width: calc(var(--index) * 12);
          &:hover {
            transform: scale3d(1, 1, 1);
            img {
              filter: opacity(0.5) grayscale(0.8);
            }
          }
          &:target {
            transform: scale3d(1.1, 1.1, 1.1);
            img {
              filter: opacity(1) grayscale(0);
            }
          }
        }
      }
    }
    .section_right {
      grid-area: 2 / 1 / 3 / 2;
      width: 95%;
      height: calc(var(--index)*21);
      .SkillsH1 {
        font-size: var(--index);
      }
      .skill_container {
        width: 100%;
        .skill {
          width: 100%;
          .skill_progress {
            .skill_progress_boxes {
              div {
                width: 95%;
              }
            }
          }
        }
      }
    }
  }
  .contacts__section {
    .contacts_form {
      .linear_background {
        flex-direction: column-reverse;
        .contacts_sendMessage .send_form {
          font-size: var(index);
          h2 {
            font-size: calc(var(--index));
          }
          div {
            input {
              font-size: calc(var(--index) * 0.9);
            }
          }
          #send_message {
            font-size: calc(var(--index) * 0.9);
          }
          .send_button {
            height: calc(var(--index) * 2);
            width: calc(var(--index) * 6);
          }
        }

        .contacts_number_email {
          font-size: calc(var(--index));
          justify-content: center;
          flex-direction: row;
          width: 100%;
          .contacts__number {
            gap: calc(var(--index));
          }
          .con_top {
            flex-direction: column;
            align-items: center;
            margin: 0 auto;
            .contacts_h3 {
              font-size: calc(var(--index) * 0.9);
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 458px){
  .main-header {
    .layers {
      .layer__base {
        background-position: 60% 100%;
      }
      .layer__middle {
        background-position: 60% 100%;
      }
      .layer__header {
        padding-bottom: calc(var(--index) * 20);
        .layer__caption {
          font-size: calc(var(--index) * 4 );
          letter-spacing: calc(var(--index)/2.2);
          text-shadow: 0 0 20px #ff681a;
        }
        .layer__tittle {
          font-size: calc(var(--index)/1.25);
          font-weight: 600;
        }
      }
    }
  }
}




